}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .person-details-content-cast-movies-cast-item .person-details-content-cast-movies-cast-item-footer p span {
    display: flex; align-items: center;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .person-details-content-cast-movies-cast-item .person-details-content-cast-movies-cast-item-footer p span svg {
    margin-right: .5rem;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .person-details-content-cast-movies-cast-item .person-details-content-cast-movies-cast-item-footer p span.HiClock svg {
    color: var(--main-red); 
    opacity: .65; margin-top: .2rem;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .person-details-content-cast-movies-cast-item .person-details-content-cast-movies-cast-item-footer p span.IoIosStar svg {
    color: gold; margin-top: .2rem;
}


#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-track {
    display: flex !important; margin: 0 !important;
    align-content: center;
    position: relative; 
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-prev {
    left: 95.5% !important;
    top: -30% !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-next {
    left: 97.5% !important;
    top: -30% !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-prev,
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-next {
    transform: translate(-50%, -50%) !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-prev::before {
    content: '❮' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-next::before {
    content: '❯' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-arrow {
    font-size: 0 !important;
    width: 0 !important; height: 0 !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-arrow:hover {
    color: var(--main-red) !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-prev:hover::before,
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-next:hover::before {
    cursor: pointer;
    color: var(--main-red) !important;
}
#person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .slick-slider .slick-list .slick-track .slick-slide {
    width: 200px !important;
}




